import "./Navbar.scss"
import { Link } from "react-router-dom"

export default function Navbar() {
    return (
        <div className="nav">
            <div className="container">

                <div className="nav__content">

                    <Link to="/" className="nav__logo">Cooking-ninja</Link>
                    <Link to="/create" className="nav__link btn btn-success">create</Link>
                </div>
            </div>
        </div>
    )
}
