import "./SingleRecipe.scss"
import { Link } from "react-router-dom"


export default function SingleRecipe({ data, isPending, error}) {
    return (
        <>
            {isPending && <h1>Loading...</h1>}
            {error && <h1>Error</h1>}
            <div className="recipe__single">
                <h1 className="recipe__title">{data.title}</h1>
                <h4 className="recipe__time">{data.cookingTime} to cook</h4>
                <p className="recipe__description">{data.method.substring(0, 100)}...</p>

                <Link to={`/recipe/${data.id}`} >More Details</Link>

            </div>
        </>
    )
}
