import "./Create.scss"
import { useFetch } from "../../Hooks/useFetch"
import { useState } from "react";
import { useNavigate } from "react-router-dom";


export default function Create() {

    const [title, setTitle] = useState("")
    const [time, setTime] = useState("")
    const [method, setMethod] = useState("")
    const [singleIng, setSingleIng] = useState("")
    const [ingredients, setIngredients] = useState([])

    const navigate = useNavigate()

    const { postData } = useFetch("https://my-json-server.typicode.com/Jaseem-v/cooking-recipe-db/recipes", "POST")

    const addIngredients = (e) => {
        e.preventDefault()
        if (!ingredients.includes(singleIng)) {
            setIngredients(prev => [...prev, singleIng])
        }

        setSingleIng("")

    }

    const handleSubmit = (e) => {
        e.preventDefault()
        console.log(time, title, method);
        postData({ title, method, cookingTime : time + "minutes" ,ingredients})

        setTitle("")
        setTime("")
        setMethod("")

        // navigate("/")
    }

    return (
        <div>
            <form onSubmit={handleSubmit}>
                <label className="create__label">
                    <span className="create__title" >Title</span>
                    <input type="text" className="create__input" value={title}
                        onChange={(e) => setTitle(e.target.value)} required />
                </label>

                <label className="create__label">
                    <span className="create__title">cooking Time</span>
                    <input type="number" className="create__input" value={time}
                        onChange={(e) => setTime(e.target.value)} required />
                </label>
                <label className="create__label">
                    <span className="create__title">Ingredients</span>
                    <div className="create__ingredients">
                        <input type="text" className="create__input" value={singleIng}
                            onChange={(e) => setSingleIng(e.target.value)} />
                        <button className="btn btn-secondary" onClick={addIngredients} > add </button>
                    </div>

                    <span style={{ marginTop: "10px", color: "gray", display: "block" }}>ingredients : {ingredients.map((el, _, arr) => el + `${arr[arr.length - 1] == el ? " . " : " , "}`)}  </span>
                </label>
                <label className="create__label">
                    <span className="create__title">Method</span>
                    <textarea className="create__input" value={method}
                        onChange={(e) => setMethod(e.target.value)} />
                </label>

                <button type="submit" className="btn btn-submit">submit</button>
            </form>
        </div>
    )
}
