import { useParams } from "react-router-dom"
import { useFetch } from "../../Hooks/useFetch"



export default function Details() {

    const { id } = useParams()

    const { data, isPending, error } = useFetch("https://my-json-server.typicode.com/Jaseem-v/cooking-recipe-db/recipes/" + id)



    return (
        <div> 
            {isPending && <h1>Loading...</h1>}
            {error && <h1>Error</h1>}
            {data && <>
                <h1 className="recipe__title">{data.title}</h1>
                <h4 className="recipe__time">{data.cookingTime} to cook</h4>
                <p className="recipe__description">{data.method}</p>

                <h3>{data.ingredients.map(el => el)}</h3>
            </>}


        </div>
    )
}
