import SingleRecipe from "../../Components/SingleRecipe"
import "./Home.scss";
import { useFetch } from "../../Hooks/useFetch"


export default function Home() {

    const { data, isPending, error } = useFetch("https://my-json-server.typicode.com/Jaseem-v/cooking-recipe-db/recipes")

    return (
        <div className="recipe__grid">
            {data && data.map((el) => {
                return <SingleRecipe key={el.id} data={el} isPending={isPending} error={error} />
            })}


        </div>
    )
}
